#wrap {
  min-width: 1900px;
  min-height: 100dvh;
  position: relative;
  .content {
    width: 100%;
    display: flex;
    min-height: calc(100vh - 123px);
  }
  #aside {
    flex: 0 0 260px;
    border-right: 1px solid #e5e8eb;
    padding: 12px 30px;
    .menu-list {
      > li {
        border-bottom: 1px solid #e5e8eb;
        cursor: pointer;
        &:last-child {
          border-bottom: 0;
        }
        &.active {
          a {
            color: #009881;
          }
          .dep2 {
            display: block;
          }
        }
      }
      .dep1 {
        display: flex;
        align-items: center;
        justify-content: space-between;
        font-weight: 500;
        padding: 24px 0;
        &.active {
          .icon-aside-arrow {
            background-image: url("../images/icon-arrow-active.svg");
          }
          & + .dep2 {
            opacity: 1;
            display: block;
            padding-bottom: 23px;
          }
        }
      }
      .dep2 {
        display: none;
        opacity: 0;
        > li {
          width: 100%;
          padding-bottom: 23px;
          &.active {
            margin: -10px 0;
          }
          a {
            font-size: 14px;
            color: #777;
            font-weight: 500;
            padding: 11px 15px;
          }
          &.active {
            a {
              color: #009881;
              display: block;
              position: relative;
              &::before {
                content: "";
                width: 100%;
                height: 100%;
                background-color: #f1f3f4;
                border-radius: 8px;
                position: absolute;
                top: 0;
                left: 0;
                z-index: -1;
              }
            }
          }
          &:last-child {
            padding-bottom: 0;
          }
        }
      }
    }
  }
  .sub-content {
    width: 100%;
    padding: 40px 30px 210px;
    position: relative;
  }
}
.container {
  margin-left: 0;
}
.breadcrumb {
  display: flex;
  align-items: center;
  justify-content: end;
  gap: 35px;
  .breadcrumb-item {
    position: relative;
    color: #444;
    font-size: 13px;
    a {
      color: #444;
      font-size: 13px;
    }
    &::after {
      content: "";
      width: 6px;
      height: 10px;
      background-image: url("../images/icon-breadcrumb-arrow.png");
      position: absolute;
      top: 50%;
      transform: translateY(-50%);
      right: -20px;
      background-size: cover;
    }
    &:last-child {
      &::after {
        content: none;
      }
    }
  }
}

.filter-wrap {
  border: 1px solid #ebebeb;
  background-color: #fafafa;
  padding: 20px 20px 20px 30px;
  border-radius: 8px;
  margin-top: 45px;
  margin-bottom: 100px;
  > .container {
    width: 100%;
    max-width: 100%;
  }
  .filter-container {
    display: flex;
    justify-content: space-between;
  }
  .filter-content {
    display: flex;
  }
  .filter-flex {
    height: 100%;
    display: flex;
    flex-direction: column;
    gap: 20px;
  }
  .search-btn {
    width: 100px;
    height: 100%;
    max-height: 100px;
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 7px;
    &.lg {
      height: 108px;
    }
  }
}

.head-content {
  display: flex;
  justify-content: space-between;
  .main-title {
    font-size: 24px;
    color: #000;
    letter-spacing: -1.2px;
    font-weight: 700;
  }
}
.progress-wrap {
  position: fixed;
  width: 100vw;
  height: 100vh;
  top: 0;
  left: 0;
  &::before {
    content: "";
    width: 100vw;
    height: 100vh;
    background-color: #000;
    opacity: 0.6;
    z-index: 1;
    position: absolute;
  }
}
.progress-bar {
  width: 400px;
  background-color: #ced6e5;
  border-radius: 2px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 9999;
}
.progress-file-name {
  position: absolute;
  top: 48.5%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 9999;
  font-size: 14px;
  color: #fff;
}
.progress-text {
  position: absolute;
  top: 52.5%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 9999;
  font-size: 14px;
  color: #fff;
}
.progress {
  width: 0;
  height: 5px;
  border-radius: 2px;
  background-color: #14907f; /* 게이지 색상 */
  text-align: center;
  line-height: 5px;
  color: white;
  @include media-breakpoint-down(md) {
    height: 1.7px;
  }
}

.loading-wrap {
  position: fixed;
  width: 100vw;
  height: 100vh;
  top: 0;
  left: 0;
  &::before {
    content: "";
    width: 100vw;
    height: 100vh;
    background-color: #000;
    opacity: 0.6;
    z-index: 1;
    position: absolute;
  }
  .loading-box {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    z-index: 9999;
  }
  .loading {
    border: 4px solid #fff;
    border-top-color: #00b8a3;
    border-radius: 100%;
    height: 82px;
    width: 82px;
    animation: rotate 2s infinite linear;
  }

  @keyframes rotate {
    from {
      transform: rotate(0deg);
    }
    to {
      transform: rotate(359deg);
    }
  }
  .loading-text {
    font-size: 14px;
    color: #fff;
    display: block;
    text-align: center;
    margin-top: 14px;
    font-weight: 500;
  }
}
