#header {
  .top-head {
    height: 80px;
    background-image: url("../images/depth1-bg.png");
    padding: 0 40px 0 30px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    background-size: cover;
    .left-content {
      display: flex;
      align-items: center;
      gap: 140px;
      padding: 20px 0;
    }
  }
  .gnb-depth1 {
    display: flex;
    align-items: center;
    gap: 3px;
    margin-bottom: -25px;
    li {
      min-width: 150px;
      a {
        color: #111;
        padding: 19px 16px;
        display: block;
        font-size: 16px;
        background-image: url("../images/gnb-menu1.png");
        background-size: cover;
        line-height: 1;
        font-weight: 500;
        border-radius: 8px 8px 0 0;
        text-align: center;
      }
      &.active {
        a {
          background-color: #0d1517;
          color: #fff;
          background-image: none;
        }
      }
    }
  }
  .user-info {
    display: flex;
    align-items: center;
    gap: 30px;
    padding: 20px 0;
    .portal-link {
      display: flex;
      align-items: center;
      gap: 8px;
      font-size: 15px;
      line-height: 1;
      color: #fff;
      margin-right: 4px;
      &.dashboard {
        border-radius: 8px;
        position: relative;
        z-index: 1;
        padding: 7px 10px;
        &::before {
          content: "";
          position: absolute;
          top: 0;
          left: 0;
          width: 100%;
          height: 100%;
          background-image: url("../images/portal-bg.png");
          background-size: cover;
          background-position: center;
          z-index: -1;
          border-radius: 8px;
        }
      }
      &.management {
      }
    }
    .sign-btn {
      background-color: #0d1517;
      font-size: 14px;
      border-radius: 8px;
    }
  }
  .info {
    font-size: 14px;
    font-weight: 500;
    color: #ffffff;
    display: flex;
    align-items: center;
    gap: 20px;
    .user-id {
      position: relative;
      &::before {
        content: "";
        display: block;
        width: 1px;
        height: 10px;
        background-color: #fff;
        opacity: 0.6;
        left: -10px;
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
      }
    }
  }
  .gnb-depth2 {
    height: 54px;
    display: flex;
    align-items: center;
    gap: 65px;
    background-color: #282a35;
    padding: 15px 30px;
    li {
      position: relative;
      a {
        color: #fff;
        font-size: 14px;
        font-weight: 500;
        line-height: 1.86;
        position: relative;
      }
      &:hover {
        a {
          &::after {
            content: "";
            position: absolute;
            width: 100%;
            height: 3px;
            background-color: #10b497;
            left: 0;
            bottom: -10px;
            border-radius: 0.5px;
          }
        }
      }
      &.active {
        a {
          &::after {
            content: "";
            position: absolute;
            width: 100%;
            height: 3px;
            background-color: #10b497;
            left: 0;
            bottom: -10px;
          }
        }
      }
    }
  }
}
